body {
    background: linear-gradient(-38deg, grey, grey);
    background-size: 200% 200%;
    animation: verticalBreathing 15s ease infinite;
    height: 100vh;
    overflow: auto;
  }
  
  @keyframes verticalBreathing {
    0% {
      background-position: 0% 0%;
    }
    25% {
      background-position: 40% 40%;
    }
    50% {
      background-position: 80% 80%;
    }
    75% {
      background-position: 20% 60%;
    }
    100% {
      background-position: 0% 0%;
    }
  }
  
  .questionnaire {
    text-align: center;
    width: 450px;
    min-height: 200px;
    height: min-content;
    transform: translate(-50%, 50%);
    border-radius: 15px;
    padding: 20px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    display: flex;
    flex-direction: column;
    background-color: whitesmoke;
    position: relative;
    top: 5%;
    left: 50%;
    overflow-y:auto;
    overflow-x: hidden; 
  }
  
  h1 {
    font-size: 35px;
  }
  
  p {
    font-size: 30px;
  }
  
  .answer-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    position: relative;
    margin: 10px auto;
    max-width: 500px;
    row-gap: 30px;
    padding: 10px 30px;
    column-gap: 15px;
    overflow-y:auto;
    overflow-x: hidden;
  }
  
  button {
    position: relative;
    width: 150px;
    font-size: 16px;
    color: #ffffff;
    background-color: #2b3868;
    border-radius: 3px;
    padding: 5px;
    align-items: center;
    cursor: pointer;
    border: none;
    text-align: center;
  }
  .selectedButton {
    background-color: #e4c30a;
  }
  .searchButton {
    background-color: black;
  }

  

  
  @media all and (max-width: 700px) {
    .questionnaire {
      padding: 5px;
      width: 90%;
    }
    h1 {
      font-size: 30px;
    }
    p {
      font-size: 26px;
    }
    button {
      width: 100%;
      min-width: 60px;
    }
  }
  