.row{
  margin-left: 20px;
  color: white;
}
.row__posters{
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
}

.row__posters::-webkit-scrollbar{
  display: none;
}



.row__poster{
  object-fit: contain;
  width: 100%;
  max-height: 100px;
  margin-right: 10px;
  transition: transform 450ms;


}

.row__poster:hover{
  transform: scale(1.08);
}

.row__posterLarge{
  max-height: 260px;
}

.row__posterLarge:hover{
  transform:scale(1.09);

}

img:hover {
  cursor: pointer;
}

.modal-title {
  color: black;
}