.banner{
  color: white;
  object-fit: contain;
  height: 448px;

}

.banner__contents{
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.banner__title{
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner__description {
  font-size: 1.2rem;
  line-height: 1.5;

  /* Media query for screens smaller than 600px */
  @media screen and (max-width: 600px) {
    font-size: 1rem;
    line-height: 1.3;
  }

  /* Media query for screens smaller than 400px */
  @media screen and (max-width: 400px) {
    font-size: 0.8rem;
    line-height: 1.1;
  }
}

.banner__button{
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;

}

.banner__button:hover{
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}


